import React from 'react';
import s from './Dialogs.module.css';
import {DialogItem} from "./DialogItem/DialogItem";
import {Message} from "./Message/Message";
import {DialogItemType, MessageType} from "../../App";

type DialogsType = {
    dialogs: Array<DialogItemType>
    messages: Array<MessageType>
}

const Dialogs = (props: DialogsType) => {
    const dialogsElements = props.dialogs.map(d => <DialogItem id={d.id} name={d.name}/>);
    const messagesElements = props.messages.map(m => <Message message={m.message}/>);

    return (
        <div className={s.dialogs}>
            <ul className={s.dialogsItems + ' ' + s.listReset}>
                {dialogsElements}
            </ul>

            <ul className={s.messages + ' ' + s.listReset}>
                {messagesElements}
            </ul>
        </div>
    )
};

export default Dialogs;
